import { writable } from 'svelte/store';
// Simple, reliable router store
export const currentRoute = writable('overview');
// Simple navigation function
export function navigateTo(route) {
    console.log('Simple router: Navigating to:', route);
    // Update the store immediately
    currentRoute.set(route);
    // Update URL
    if (typeof window !== 'undefined') {
        const paths = {
            overview: '/',
            connections: '/connections',
            dnp: '/dnp',
            enforcement: '/enforcement',
            community: '/community',
            profile: '/profile'
        };
        const path = paths[route] || '/';
        window.history.pushState({ route }, route, path);
        document.title = `${route} - No Drake in the House`;
    }
}
// Initialize router
export function initRouter() {
    if (typeof window !== 'undefined') {
        // Handle browser back/forward
        window.addEventListener('popstate', (event) => {
            const path = window.location.pathname;
            let route = 'overview';
            if (path === '/connections')
                route = 'connections';
            else if (path === '/dnp')
                route = 'dnp';
            else if (path === '/enforcement')
                route = 'enforcement';
            else if (path === '/community')
                route = 'community';
            else if (path === '/profile')
                route = 'profile';
            currentRoute.set(route);
        });
        // Set initial route
        const path = window.location.pathname;
        let initialRoute = 'overview';
        if (path === '/connections')
            initialRoute = 'connections';
        else if (path === '/dnp')
            initialRoute = 'dnp';
        else if (path === '/enforcement')
            initialRoute = 'enforcement';
        else if (path === '/community')
            initialRoute = 'community';
        else if (path === '/profile')
            initialRoute = 'profile';
        currentRoute.set(initialRoute);
    }
}
